.product-list {
  display: flex;
  width: 93%;
  margin: 20px auto 0 auto;
  &__filter {
    margin-top: 30px;
    padding-top: 10px;
    width: 25%;
    @media screen and (max-width: 770px) {
      display: none;
    }
    &-text {
      color: #455a64;
      font-size: 17px;
      font-weight: 600;
      margin: 20px 0 20px 0;
    }
    &-checkbox {
      display: flex;
      flex-wrap: wrap;
      gap: 15px;
      margin: 20px 0;
    }
  }
  &__cart {
    width: 75%;
    @media screen and (max-width: 770px) {
      width: 100% !important;
    }
    &-filter {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 5px;
      padding: 20px;
      margin-bottom: 30px;
      @media screen and (max-width: 770px) {
        justify-content: space-between !important;
        padding: 0px;
      }

      &__text {
        font-size: 12px;
        color: #455a64;
        font-weight: 600;
      }
      &__select {
        font-family: "Montserrat", sans-serif;
        padding: 6px;
        font-weight: 500;
        border-radius: 4px;
        color: #455a64;
        background-color: #f5f7f8;
        border: none !important;
        outline: none !important;
        @media screen and (max-width: 625px) {
          padding: 6px 30px;
          text-align: center;
          font-size: 12px;
        }
      }
    }
    &-content {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 24px;
      width: 100%;
      @media screen and (max-width: 1284px) {
        justify-content: space-between !important;
      }
      @media screen and (max-width: 470px) {
        justify-content: center !important;
      }
    }
  }
  .mobile-btn{
    display: none;
    @media screen and (max-width: 770px) {
      display: block;
    }
  }
}
